import React, { useState, useRef } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper/modules';
import 'swiper/swiper-bundle.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import './Style.css';
import { OverlayTrigger, Popover } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom'; // Import useNavigate

const NumberRating = ({ fieldName, value, onChange, max }) => {
  const [hoverValue, setHoverValue] = useState(null);
  const [clickedValue, setClickedValue] = useState(value);

  const getBoxColor = (i) => {
    if (clickedValue !== null) {
      if (i <= clickedValue) {
        if (i <= 3) return 'red';
        if (i <= 6) return 'orange';
        return 'green';
      }
    } else if (hoverValue !== null && i <= hoverValue) {
      if (i <= 3) return 'red';
      if (i <= 6) return 'orange';
      return 'green';
    }
    return '#ddd';
  };

  const handleClick = (i) => {
    if (clickedValue === 0) {
      console.log(`${fieldName} updates to ${i}`);
    } else {
      console.log(`${fieldName} Changed to ${i}`);
    }

    setClickedValue(i);
    onChange(i);
  };

  return (
    <div className="number-rating">
      {[...Array(max)].map((_, i) => (
        <div
          key={i}
          className="rating-box"
          style={{
            backgroundColor: getBoxColor(i + 1),
          }}
          onMouseEnter={() => setHoverValue(i + 1)}
          onMouseLeave={() => setHoverValue(null)}
          onClick={() => handleClick(i + 1)}
        >
          {i + 1}
        </div>
      ))}
    </div>
  );
};

const SurveyWithSwiper = () => {
  const navigate = useNavigate(); // Call useNavigate at the top level
  const [showWelcome, setShowWelcome] = useState(true);
  const [currentSlide, setCurrentSlide] = useState(1);
  const [questions, setQuestions] = useState([
    { text: 'How satisfied are you with our products?', fieldName: 'productSatisfaction', type: 'rating' },
    { text: 'How fair are the prices compared to similar retailers?', fieldName: 'priceFairness', type: 'rating' },
    { text: 'How satisfied are you with the value for money of your purchase?', fieldName: 'valueForMoney', type: 'rating' },
    { text: 'On a scale of 1-10, how likely are you to recommend us to your friends and family?', fieldName: 'recommendScore', type: 'rating' },
    { text: 'What could we do to improve our service?', fieldName: 'improvementSuggestion', type: 'text' }
  ]);
  const [newQuestionText, setNewQuestionText] = useState('');
  const [newQuestionField, setNewQuestionField] = useState('');
  const [newQuestionType, setNewQuestionType] = useState('rating');
  const [isFormVisible, setIsFormVisible] = useState(false);
  const totalSlides = questions.length;
  const swiperRef = useRef(null);

  const [surveyAnswers, setSurveyAnswers] = useState({
    productSatisfaction: 0,
    priceFairness: 0,
    valueForMoney: 0,
    recommendScore: 0,
    improvementSuggestion: '',
  });

  const handleStartClick = () => {
    setShowWelcome(false);
  };

  const handleRatingChange = (fieldName) => (value) => {
    setSurveyAnswers({ ...surveyAnswers, [fieldName]: value });
  };

  const handleSkipClick = () => {
    if (swiperRef.current) {
      swiperRef.current.slideNext();
    }
  };

  const handleSubmit = () => {
    console.log('Submitted Survey:', surveyAnswers);
    navigate('/thankyou'); // Use navigate here
    setSurveyAnswers({
      productSatisfaction: 0,
      priceFairness: 0,
      valueForMoney: 0,
      recommendScore: 0,
      improvementSuggestion: '',
    });
    setShowWelcome(true);
  };

  const handleAddQuestion = () => {
    if (newQuestionText && newQuestionField) {
      setQuestions([...questions, { text: newQuestionText, fieldName: newQuestionField, type: newQuestionType }]);
      setNewQuestionText('');
      setNewQuestionField('');
      setNewQuestionType('rating');
      setIsFormVisible(false);
    }
  };

  const popover = (
    <Popover id="popover-basic">
      <Popover.Body>
        <p>Are you sure? You won't be able to make any changes after submitting.</p>
        <button className="btn btn-primary" onClick={handleSubmit}>
          Yes, Submit
        </button>
      </Popover.Body>
    </Popover>
  );

  return (
    <>
      <div className="survey-container">
        {showWelcome ? (
          <div className="welcome-screen">
            <h1>Welcome to Our Survey</h1>
            <p>We're glad you're here! Click the button below to start the survey.</p>
            <button onClick={handleStartClick} className=" btn btn-primary ">
              Start Survey
            </button>
          </div>
        ) : (
          <>
            <Swiper
              navigation={true}
              modules={[Navigation]}
              className="mySwiper"
              onSlideChange={(swiper) => setCurrentSlide(swiper.activeIndex + 1)}
              onSwiper={(swiper) => (swiperRef.current = swiper)}
            >
              {questions.map((question, index) => (
                <SwiperSlide key={index} className="Container">
                  <div className="slide-wrapper">
                    <h3>{question.text}</h3>
                    {question.type === 'rating' ? (
                      <NumberRating
                        fieldName={question.fieldName}
                        max={10}
                        value={surveyAnswers[question.fieldName] || 0}
                        onChange={handleRatingChange(question.fieldName)}
                      />
                    ) : (
                      <textarea
                        placeholder="Your suggestions"
                        value={surveyAnswers[question.fieldName] || ''}
                        onChange={(e) =>
                          setSurveyAnswers({
                            ...surveyAnswers,
                            [question.fieldName]: e.target.value,
                          })
                        }
                      />
                    )}

                    {/* Skip button to move to the next question */}
                    <button className="btn btn-warning skip-button" onClick={handleSkipClick}>
                      Skip
                    </button>

                    {/* Show the submit button on the last slide */}
                    {index === questions.length - 1 && (
                      <OverlayTrigger trigger="click" placement="top" overlay={popover}>
                        <button className="btn btn-success submit-button" onClick={handleSubmit}>Submit Survey</button>
                      </OverlayTrigger>
                    )}
                  </div>
                </SwiperSlide>
              ))}
            </Swiper>

            <div className="pagination">
              <div className="page-numbers">
                {currentSlide}/{totalSlides}
              </div>
            </div>

            <button className="btn btn-primary new-question" onClick={() => setIsFormVisible(!isFormVisible)}>
              {isFormVisible ? 'Cancel' : 'Add Question'}
            </button>
          </>
        )}
      </div>

      {/* Add Question form */}
      {isFormVisible && (
        <div className="add-question-form">
          <h4>Add New Question</h4>
          <input
            type="text"
            placeholder="Question text"
            value={newQuestionText}
            onChange={(e) => setNewQuestionText(e.target.value)}
          />
          <input
            type="text"
            placeholder="Field name"
            value={newQuestionField}
            onChange={(e) => setNewQuestionField(e.target.value)}
          />
          <select
            value={newQuestionType}
            onChange={(e) => setNewQuestionType(e.target.value)}
          >
            <option value="rating">Rating</option>
            <option value="text">Text</option>
          </select>
          <button onClick={handleAddQuestion} className="btn btn-primary">
            Add Question
          </button>
        </div>
      )}
    </>
  );
};

export default SurveyWithSwiper;
